___CSS_LOADER_EXPORT___.push([module.id, ".tab-content[data-v-3eb45551]{margin-top:10px;padding:0 10px}", "",{"version":3,"sources":["webpack://./resources/apps/admin/setting/SettingsMain.vue"],"names":[],"mappings":"AAkGA,8BACA,eAAA,CACA,cACA","sourcesContent":["<template>\n    <div>\n        <header class=\"page-header\">\n            <div class=\"page-title\">设置</div>\n        </header>\n        <div class=\"mainframe-content\">\n            <div class=\"content-block\">\n                <el-tabs>\n                    <el-tab-pane label=\"基本设置\">\n                        <div class=\"tab-content\">\n                            <settings-basic :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                    <el-tab-pane label=\"注册设置\">\n                        <div class=\"tab-content\">\n                            <settings-register :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                    <el-tab-pane label=\"附件设置\">\n                        <div class=\"tab-content\">\n                            <settings-attach :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                    <el-tab-pane label=\"水印设置\">\n                        <div class=\"tab-content\">\n                            <settings-water :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                    <el-tab-pane label=\"微信公众号\">\n                        <div class=\"tab-content\">\n                            <settings-wechat :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                    <el-tab-pane label=\"AI设置\">\n                        <div class=\"tab-content\">\n                            <settings-open-ai :settings=\"settings\"/>\n                        </div>\n                    </el-tab-pane>\n                </el-tabs>\n            </div>\n        </div>\n        <div class=\"h40\"></div>\n        <div class=\"edit-bottom\">\n            <el-button type=\"primary\" @click=\"handleSubmit\">更新配置</el-button>\n        </div>\n    </div>\n</template>\n\n<script>\n    import SettingsBasic from \"./SettingsBasic\";\n    import SettingsRegister from \"./SettingsRegister\";\n    import SettingsAttach from \"./SettingsAttach\";\n    import SettingsWater from \"./SettingsWater\";\n    import SettingsWechat from \"./SettingsWechat\";\n    import SettingsPoster from \"./SettingsPoster\";\n    import SettingsOpenAi from \"./SettingsOpenAi\";\n\n    export default {\n        name: \"SettingsMain\",\n        components: {\n            SettingsOpenAi,\n            SettingsPoster,\n            SettingsWechat,\n            SettingsWater,\n            SettingsAttach,\n            SettingsRegister,\n            SettingsBasic\n        },\n        data() {\n            return {\n                settings: {},\n                tabStyle: {\n                    'flex': 'none',\n                    'padding-left': '15px',\n                    'padding-right': '15px'\n                }\n            }\n        },\n        methods: {\n            fetchSettings() {\n                this.$get('/common/setting.settings').then(response => {\n                    this.settings = response.result;\n                });\n            },\n            handleSubmit() {\n                let {settings} = this;\n                this.$post('/common/setting.update', {settings}).then(response => {\n                    this.$showToast('配置更新成功');\n                });\n            },\n        },\n        created() {\n            this.fetchSettings();\n        },\n    }\n</script>\n\n<style scoped>\n    .tab-content {\n        margin-top: 10px;\n        padding: 0 10px;\n    }\n</style>\n"],"sourceRoot":""}]);
